import React from 'react'
import styled from 'styled-components'
import { cond, always, equals } from 'ramda'
import {
  Input,
  Checkbox,
  FormControl,
  Select,
  LinearProgress,
  InputLabel,
  MenuItem,
  Menu,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Fab,
  withStyles
} from '@material-ui/core'

import SIP from '../../../../assets/images/sip.svg'
import Cell from '../../../../assets/images/mobile.svg'
import timeZones from '../../../../constants'
import PlusSvg from '../../svgs/Plus'

