const styles = {
  dialogPaper: { width: '100%' },
  deviceTitle: {
    padding: '60px 0',
    maxWidth: '493px',
    width: '100%',
    margin: '0 auto'
  },
  deviceTitleImage: {
    width: '45px',
    height: '44px',
    verticalAlign: 'middle',
    marginRight: '20px'
  },
  deviceTitleText: {
    color: '#898888',
    fontFamily: 'Open Sans',
    fontSize: '2rem',
    verticalAlign: 'middle'
  },
  deviceModalContent: {
    maxWidth: '600px',
    width: '100%',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  deviceModalInput: {
    fontFamily: 'Open Sans',
    fontSize: '1rem',
    '&::after': {
      borderBottom: '2px solid #09d357'
    }
  },
  deviceModalSelect: {
    '&::after': {
      borderBottom: '2px solid #09d357'
    }
  },
  deviceDialogActions: {
    justifyContent: 'space-between',
    maxWidth: '525px',
    width: '100%',
    margin: '50px auto 40px'
  },
  fab: {
    width: 139,
    height: 33,
    fontSize: '1.125rem',
    fontFamily: 'Source Sans Pro',
    textTransform: 'inherit',
    borderRadius: '29px',
    background: '#09d357',
    color: '#fff',
    '&:hover': {
      background: '#09d357',
      color: '#fff'
    }
  }
}

const initialDeviceState = {
  anchorEl: null,
  deviceType: null
}

function deviceReducer(state, action) {
  switch (action.type) {
    case 'openMenu': {
      return { ...state, anchorEl: action.anchorEl, deviceType: null }
    }
    case 'closeMenu': {
      return { ...state, anchorEl: null, deviceType: null }
    }
    case 'setDeviceType': {
      return { ...state, anchorEl: null, deviceType: action.deviceType }
    }
    default:
      throw new Error()
  }
}

const AddView = ({ onChange, classes, devices, onDeviceAdd, onDeviceToggle, directPhone }) => {
  const [password, setPassword] = React.useState('')
  const [fakeUser, setFakeUser] = React.useState('')
  const [userLevel, setUserLevel] = React.useState('')
  const [timeZone, setTimeZone] = React.useState('')
  const [ringingTime, setRingingTime] = React.useState('')
  const [language, setLanguage] = React.useState('')
  const [deviceState, dispatch] = React.useReducer(deviceReducer, initialDeviceState)

  React.useEffect(() => {
    onDeviceToggle(!deviceState.deviceType)
    if (!deviceState.deviceType) setFakeUser('')
  }, [deviceState.deviceType, onDeviceToggle])

  const handleAddDevice = () => {
    onDeviceAdd(deviceState.deviceType)
    dispatch({ type: 'closeMenu' })
  }

  const passwordLevel =
    password.length < 4 ? 0 : password.length >= 4 && password.length < 8 ? 50 : 100

  return (
    <Wrapper>
      <Dialog open={!!deviceState.deviceType} classes={{ paper: classes.dialogPaper }}>
        <DialogTitle className={classes.deviceTitle}>
          {deviceState.deviceType === 1 ? (
            <span>
              <img className={classes.deviceTitleImage} src={SIP} alt="SIP" />
              <span className={classes.deviceTitleText}>SIP</span>
            </span>
          ) : (
            <span>
              <img className={classes.deviceTitleImage} src={Cell} alt="Cell" />
              <span className={classes.deviceTitleText}>Add New Cell Phone Device</span>
            </span>
          )}
        </DialogTitle>
        <DialogContent className={classes.deviceModalContent}>
          {deviceState.deviceType === 1 ? (
            <>
              <div style={{ marginBottom: '35px', width: '493px' }}>
                <Input fullWidth className={classes.deviceModalInput} placeholder="Device Name" />
              </div>
              <div style={{ marginBottom: '35px', width: '493px' }}>
                <Input fullWidth className={classes.deviceModalInput} placeholder="SIP Username" />
              </div>
              <div style={{ marginBottom: '35px', width: '493px' }}>
                <Input fullWidth className={classes.deviceModalInput} placeholder="SIP Password" />
              </div>
              <div style={{ marginBottom: '35px', width: '493px' }}>
                <Input
                  fullWidth
                  className={classes.deviceModalInput}
                  placeholder="Retype SIP Password"
                />
              </div>
            </>
          ) : (
            <>
              <div style={{ marginBottom: '35px', width: '493px' }}>
                <Input fullWidth className={classes.deviceModalInput} placeholder="Device Name" />
              </div>
              <div style={{ marginBottom: '25px', width: '493px' }}>
                <Input fullWidth className={classes.deviceModalInput} placeholder="Number" />
              </div>
              <div style={{ marginBottom: '35px', width: '493px' }}>
                <FormControl fullWidth classes={{ root: classes.deviceModalSelect }}>
                  <InputLabel style={{ color: '#7d7d7d' }} htmlFor="add-user-level">
                    Assign to User
                  </InputLabel>
                  <Select
                    value={fakeUser}
                    onChange={e => setFakeUser(e.target.value)}
                    className={classes.deviceModalSelect}
                    inputProps={{
                      name: 'fakeUser',
                      id: 'add-user-level'
                    }}
                  >
                    <MenuItem value={''} />
                    <MenuItem value={1}>User 1</MenuItem>
                    <MenuItem value={2}>User 2</MenuItem>
                  </Select>
                </FormControl>
              </div>
            </>
          )}
        </DialogContent>
        <DialogActions className={classes.deviceDialogActions}>
          <Fab
            variant="extended"
            className={classes.fab}
            onClick={() => dispatch({ type: 'closeMenu' })}
          >
            Back
          </Fab>
          <Fab variant="extended" className={classes.fab} onClick={handleAddDevice}>
            Add Device
          </Fab>
        </DialogActions>
      </Dialog>
      <div className="aligner flex justify-between">
        <div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              name="firstName"
              onChange={onChange}
              placeholder="First Name"
              inputProps={{ tabIndex: '1' }}
            />
          </div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="email"
              name="email"
              onChange={onChange}
              placeholder="Email for login"
              inputProps={{ tabIndex: '2' }}
            />
          </div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="password"
              name="passwordConfirm"
              placeholder="Re-type Password"
              inputProps={{ tabIndex: '3' }}
            />
          </div>
          <div className="input-container checkbox-container">
            <label className="flex align-start">
              <Checkbox className="checkbox" />
              <span>Send email to alternate address</span>
            </label>
          </div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="tel"
              name="extensionNumber"
              onChange={onChange}
              placeholder="Extension number"
              inputProps={{ tabIndex: '5' }}
            />
          </div>
          <div className="input-container checkbox-container">
            <label className="flex align-start">
              <Checkbox className="checkbox" />
              <span>Include user in the company directory</span>
            </label>
          </div>
          <div className="input-container checkbox-container" style={{ marginBottom: '25px' }}>
            <FormControl fullWidth classes={{ root: 'select' }}>
              <InputLabel htmlFor="add-user-level">User Level</InputLabel>
              <Select
                value={userLevel}
                onChange={e => setUserLevel(e.target.value)}
                inputProps={{
                  name: 'userLevel',
                  id: 'add-user-level',
                  tabIndex: '6'
                }}
              >
                <MenuItem value={''} />
                <MenuItem value={1}>Admin</MenuItem>
                <MenuItem value={2}>User</MenuItem>
              </Select>
            </FormControl>
          </div>
          <div className="input-container checkbox-container" style={{ marginBottom: '25px' }}>
            <FormControl fullWidth classes={{ root: 'select' }}>
              <InputLabel htmlFor="add-language">Language</InputLabel>
              <Select
                value={language}
                onChange={e => setLanguage(e.target.value)}
                inputProps={{
                  name: 'language',
                  id: 'add-user-language',
                  tabIndex: '7'
                }}
              >
                <MenuItem value={''} />
                <MenuItem value={1}>English</MenuItem>
                <MenuItem value={2}>Thai</MenuItem>
                <MenuItem value={3}>Vietnamese</MenuItem>
                <MenuItem value={4}>Hebrew</MenuItem>
              </Select>
            </FormControl>
          </div>
        </div>
        <div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              name="lastName"
              onChange={onChange}
              placeholder="Last Name"
              inputProps={{ tabIndex: '1' }}
            />
          </div>
          <div className="input-container" style={{ marginBottom: '15px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="password"
              name="password"
              onChange={e => setPassword(e.target.value)}
              placeholder="Password"
              inputProps={{ tabIndex: '2' }}
            />
          </div>
          <div className="progress-container flex justify-between align-center">
            <LinearProgress value={passwordLevel} variant="determinate" />
            <span>
              {cond([
                [equals(0), always('Bad')],
                [equals(50), always('Good')],
                [equals(100), always('Best')]
              ])(passwordLevel)}
            </span>
          </div>
          <div className="input-container" style={{ marginBottom: '15px', marginTop: '17px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="email"
              name="notification-email"
              placeholder="Notification Email"
              inputProps={{ tabIndex: '4' }}
            />
          </div>
          <div className="input-container" style={{ marginBottom: '19px' }}>
            <FormControl fullWidth classes={{ root: 'select' }}>
              <InputLabel htmlFor="directPhone">Direct Phone</InputLabel>
              <Select
                value={directPhone}
                onChange={onChange}
                inputProps={{
                  name: 'directPhone',
                  id: 'add-direct-phone',
                  tabIndex: '5'
                }}
              >
                <MenuItem value={''} />
                <MenuItem value={'66600035090'}>+66600035090</MenuItem>
                <MenuItem value={'66600035091'}>+66600035091</MenuItem>
                <MenuItem value={'66600035092'}>+66600035092</MenuItem>
                <MenuItem value={'66600035093'}>+66600035093</MenuItem>
                <MenuItem value={'66600035094'}>+66600035094</MenuItem>
                <MenuItem value={'66600035095'}>+66600035095</MenuItem>
                <MenuItem value={'66600035096'}>+66600035096</MenuItem>
                <MenuItem value={'66600035097'}>+66600035097</MenuItem>
              </Select>
            </FormControl>
          </div>
          <div className="new-device-btn-container">
            <Button
              className="new-device-btn"
              aria-owns={deviceState.anchorEl ? 'device-menu' : undefined}
              aria-haspopup="true"
              onClick={e => dispatch({ type: 'openMenu', anchorEl: e.currentTarget })}
            >
              <PlusSvg />
              <span className="text">New Device</span>
            </Button>
            <Menu
              id="device-menu"
              anchorEl={deviceState.anchorEl}
              open={!!deviceState.anchorEl}
              onClose={() => dispatch({ type: 'closeMenu' })}
            >
              <MenuItem onClick={() => dispatch({ type: 'setDeviceType', deviceType: 1 })}>
                <img src={SIP} alt="sip" style={{ marginRight: '5px' }} />
                SIP
              </MenuItem>
              <MenuItem onClick={() => dispatch({ type: 'setDeviceType', deviceType: 2 })}>
                <img src={Cell} alt="cell" style={{ margin: '5px' }} />
                Cell Phone
              </MenuItem>
            </Menu>
            <img
              style={{
                margin: '0 0 0 15px',
                width: '20px',
                height: '20px',
                verticalAlign: 'middle',
                display: devices[1] ? 'inline-block' : 'none'
              }}
              src={SIP}
              alt="SIP"
            />
            <img
              style={{
                margin: '0 10px',
                width: '20px',
                height: '20px',
                verticalAlign: 'middle',
                display: devices[2] ? 'inline-block' : 'none'
              }}
              src={Cell}
              alt="Cell"
            />
          </div>
          <div
            className="input-container checkbox-container"
            style={{ marginTop: '3px', marginBottom: '25px' }}
          >
            <FormControl fullWidth classes={{ root: 'select' }}>
              <InputLabel htmlFor="add-time-zone">Time Zone</InputLabel>
              <Select
                value={timeZone}
                onChange={e => setTimeZone(e.target.value)}
                inputProps={{
                  name: 'timeZone',
                  id: 'add-time-zone',
                  tabIndex: '6'
                }}
              >
                <MenuItem value={''} />
                {timeZones.map((zone, index) => (
                  <MenuItem key={zone} value={index}>
                    {zone}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </div>
          <div className="input-container checkbox-container" style={{ marginBottom: '25px' }}>
            <FormControl fullWidth classes={{ root: 'select' }}>
              <InputLabel htmlFor="add-ringing-time">Ringing Time (Seconds)</InputLabel>
              <Select
                value={ringingTime}
                onChange={e => setRingingTime(e.target.value)}
                inputProps={{
                  name: 'ringingTime',
                  id: 'add-ringing-time',
                  tabIndex: '7'
                }}
              >
                <MenuItem value={''} />
                <MenuItem value={1}>15 sec</MenuItem>
                <MenuItem value={2}>20 sec</MenuItem>
                <MenuItem value={3}>25 sec</MenuItem>
                <MenuItem value={4}>30 sec</MenuItem>
              </Select>
            </FormControl>
          </div>
        </div>
      </div>
    </Wrapper>
  )
}

export default withStyles(styles)(AddView)
