import React from 'react'
import styled from 'styled-components'
import { compose } from 'ramda'
import { withRouter } from 'react-router-dom'
import { Checkbox, Fab, withStyles } from '@material-ui/core'
import { FormattedMessage } from 'react-intl'
import classNames from 'classnames'

import Formatted from '../../components/Formatted'
import { BasicInput } from '../../components/Inputs'
import injectFormatMessage from '../../hocs/injectFormatMessage'
import ROUTES from '../routes'

const Wrapper = styled.div`
  padding: 23vh 100px 0 0;

  .aligner {
    max-width: 400px;
    width: 100%;
    margin: 0 auto;

    > h3 {
      font-family: 'Open Sans';
      font-size: 2.1875rem;
      font-weight: 700;
      letter-spacing: 7px;
      text-transform: uppercase;
      text-align: center;
      color: #727272;
    }

    > label:nth-of-type(1) {
      display: block;
      margin: 16px auto 50px;
      opacity: 0.5;
      color: #4d4f5c;
      text-align: center;
      font-family: 'Open Sans';
      font-size: 1.125rem;
    }
  }

  .checkbox-container {
    margin: 10px 0 70px;
    font-family: 'Open Sans';

    label {
      font-size: 0.9375rem;
      color: #43425d;
      cursor: pointer;
      user-select: none;

      > span {
        vertical-align: middle;
      }
    }

    .fake-anchor {
      font-size: 0.9375rem;
      color: #43425d;
      cursor: pointer;
      user-select: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  @media (max-width: 1200px) {
    padding: 15vh 10px 0;
  }
`

const styles = {
  checkbox: {
    color: '#09d357 !important',
    padding: '0 10px 0 0'
  },
  fab: {
    width: 185,
    height: 51,
    fontSize: '18px',
    fontFamily: 'Source Sans Pro',
    textTransform: 'inherit',
    boxShadow: 'none'
  },
  login: {
    background: '#09d357',
    color: '#fff',
    '&:hover': {
      background: '#09d357',
      color: '#fff'
    }
  },
  signUp: {
    background: '#fff',
    color: '#09d357',
    border: '1px solid #09d357',
    '&:hover': {
      background: '#09d357',
      color: '#fff'
    }
  }
}

class AuthForm extends React.Component {
  state = {
    userName: '',
    password: ''
  }

  handleInputChange = event => this.setState({ [event.target.name]: event.target.value })

  handleClick = () => this.props.history.push(ROUTES.wizard)

  render() {
    const { classes, formatMessage } = this.props

    return (
      <Wrapper>
        <div className="aligner">
          <Formatted.h3 id="global.telco" />
          <Formatted.label htmlFor="userName" id="landing.welcome.authForm.label" />
          <form>
            <BasicInput
              fullWidth
              id="userName"
              onChange={this.handleInputChange}
              type="text"
              placeholder={formatMessage({ id: 'global.userName' })}
              className={classes.input}
              inputProps={{
                'aria-label': 'Username'
              }}
            />
            <BasicInput
              fullWidth
              onChange={this.handleInputChange}
              type="password"
              placeholder={formatMessage({ id: 'global.password' })}
              className={classes.input}
              inputProps={{
                'aria-label': 'Password'
              }}
            />
            <div className="checkbox-container flex justify-between align-center">
              <label>
                <Checkbox className={classes.checkbox} />
                <FormattedMessage id="global.credentials.rememberMe" />
              </label>
              <span className="fake-anchor">
                <FormattedMessage id="global.credentials.forgotPassword" />
              </span>
            </div>
            <div className="form-btns flex justify-between">
              <Fab
                variant="extended"
                aria-label="Login"
                className={classNames(classes.fab, classes.login)}
                onClick={this.handleClick}
              >
                <FormattedMessage id="global.credentials.login" />
              </Fab>
              <Fab
                variant="extended"
                aria-label="Sign up"
                className={classNames(classes.fab, classes.signUp)}
                onClick={this.handleClick}
              >
                <FormattedMessage id="global.credentials.signUp" />
              </Fab>
            </div>
          </form>
        </div>
      </Wrapper>
    )
  }
}

const enhance = compose(
  injectFormatMessage,
  withRouter,
  withStyles(styles)
)

export default enhance(AuthForm)
