/** @flow */

const NAME = `[SERVICE_SPECS]`

/**
 * Fills admin service specifications on wizard step 2
 */

export type ServiceSpecs = {
  numberOfUsers: number,
  twoWayTrunks: number,
  inboundTrunks: number,
  outboundTrunks: number,
  currencyEnum: number,
  ballance: number,
  permitPerMinuteCalls: boolean,
  callRecordingStorage: number
}

export const FILL_SERVICE_SPECS = `${NAME} FILL_SERVICE_SPECS`
export function fillServiceSpecs(payload: ServiceSpecs) {
  return {
    type: FILL_SERVICE_SPECS,
    payload
  }
}
