/** @flow */

import React from 'react'
import styled from 'styled-components'
import { connect } from 'react-redux'
import { cond, equals, always, T } from 'ramda'
import { FormattedMessage } from 'react-intl'
import Stepper from './Stepper'

import PenSvg from '../svgs/Pen'
import SettingsSvg from '../svgs/Settings'
import CardSvg from '../svgs/Card'

const Wrapper = styled.div`
  position: relative;
  max-width: 690px;
  width: 100%;
  margin: 0 auto;
`

//$FlowFixMe
const StepProgress = styled.div`
  position: absolute;
  top: 32%;
  left: 50%;
  transform: translate(-50%, 0);
  width: 85%;
  height: 3px;
  background: #e2e0e0;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: ${({ progress }) => progress || 0};
    height: 3px;
    background: #09d357;
  }
`

type Props = {
  className?: string,
  step: number
}

class Steppers extends React.PureComponent<Props> {
  static defaultProps = {
    classNames: ''
  }

  getProgressWidth = cond([
    [equals(0), always('0%')],
    [equals(1), always('25%')],
    [equals(2), always('50%')],
    [equals(3), always('100%')],
    [T, always('0%')]
  ])

  render() {
    const { className, step } = this.props

    return (
      <Wrapper className={className}>
        <StepProgress progress={this.getProgressWidth(step)} />
        <div className="steppers-container flex justify-between">
          <Stepper
            active={step === 0 || step === 1}
            color="#09d357"
            icon={<PenSvg />}
            label={<FormattedMessage id="wizard.registration" />}
          />
          <Stepper
            active={step === 2}
            color="#09d357"
            icon={<SettingsSvg />}
            label={<FormattedMessage id="wizard.specification" />}
          />
          <Stepper
            active={step === 3}
            color="#09d357"
            icon={<CardSvg />}
            label={<FormattedMessage id="wizard.payment" />}
          />
        </div>
      </Wrapper>
    )
  }
}

type MapStateToProps = (
  state: Object
) => {
  step: number
}

const mstp: MapStateToProps = ({ wizard }) => ({ step: wizard.step })

export default connect(mstp)(Steppers)
