/** @flow */

const NAME = '[WIZARD]'

/**
 * Changes wizard step to show appropriate screen eventualy
 */

export const STEP_CHANGE = `${NAME} STEP_CHANGE`
export function changeStep(step: number) {
  return {
    type: STEP_CHANGE,
    step
  }
}

export const RESET_WIZARD = `${NAME} RESET_WIZARD`
export function resetWizard() {
  return {
    type: RESET_WIZARD
  }
}
