/** @flow */

import React from 'react'
import styled from 'styled-components'
import classNames from 'classnames'

//$FlowFixMe
const Wrapper = styled.div`
  display: inline-block;

  .icon-container {
    width: 74px;
    height: 74px;
    margin: 0 auto;

    .icon {
      position: relative;
      min-width: 85%;
      min-height: 85%;
      background: #e2e0e0;
      border-radius: 50%;
      transition: transform 0.1s;

      > * {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 45%;
        max-height: 45%;
      }
    }
  }

  .label-container {
    margin: 15px 0 0;
    font-family: Roboto;
    font-size: 1.0625rem;
    text-align: center;
    opacity: 0.3;
    color: #000;
    transition: opacity: 0.1s;
  }

  &.active {
    .icon {
      transform: scale(1.2);
      background: ${({ color }) => color};
    }

    .label-container {
      opacity: 1;
    }
  }
`

type Props = {
  active: boolean,
  className?: string,
  icon: React$Node,
  color: string,
  label: React$Node
}

const Stepper = ({ active, className = '', label, color, icon }: Props) => (
  <Wrapper className={classNames(className, { active })} color={color}>
    <div className={'icon-container flex justify-center align-center'}>
      <div className="icon">{icon}</div>
    </div>
    <div className="label-container">{label}</div>
  </Wrapper>
)

export default Stepper
