/** @flow */

import isNil from 'lodash/isNil'
import * as ServiceSpecsType from '../serice-specs/action'
import * as ActionTypes from './action'

const initialState = {
  numberOfUsers: 0,
  users: [],
  isModalOpen: false,
  target: null,
  view: null,
  featureId: null
}

export default function users(state: Object = initialState, action: Object) {
  switch (action.type) {
    case ActionTypes.ADD_USER: {
      if (state.view === 'add') {
        return {
          ...state,
          users: [
            ...state.users,
            {
              ...action.payload,
              id: state.users.length + 1,
              features: {}
            }
          ],
          target: {
            ...action.payload,
            id: state.users.length + 1,
            features: {}
          }
        }
      }
      return state
    }
    case ActionTypes.TOGGLE_FEATURE: {
      return {
        ...state,
        users: state.users.map(user =>
          user.id === action.userId
            ? {
                ...user,
                features: { ...user.features, [action.featureId]: action.checked }
              }
            : user
        )
      }
    }
    case ActionTypes.TOGGLE_USER_MODAL: {
      const isClosing = (!isNil(action.force) && !action.force) || state.isModalOpen
      return {
        ...state,
        featureId: isClosing ? null : action.featureId,
        isModalOpen: isClosing ? false : true,
        target: isClosing ? null : state.users.find(({ id }) => id === action.id),
        view: isClosing ? null : action.view
      }
    }
    case ServiceSpecsType.FILL_SERVICE_SPECS:
      return {
        ...state,
        numberOfUsers: Number.isInteger(Math.floor(action.payload.numberOfUsers))
          ? action.payload.numberOfUsers
          : state.numberOfUsers
      }
    default:
      return state
  }
}
