/** @flow */

import merge from 'lodash/merge'
import { DEFAULT_LOCALE, CURRENCIES } from '../../i18n'
import * as ServiceSpecsTypes from '../serice-specs/action'
import * as ActionTypes from './action'

const initialState = {
  firstName: '',
  lastName: '',
  email: '',
  adminPassword: '',
  confirmPassword: '',
  password: '',
  contactNumber: '',
  companyName: '',
  accountDomain: '',
  address: '',
  zip: '',
  city: '',
  state: '',
  timeZone: '',
  language: DEFAULT_LOCALE,
  billingAddress: {},
  paymentDetails: {},
  balance: 0,
  currency: CURRENCIES.usd
}

export default function account(state: Object = initialState, action: Object) {
  switch (action.type) {
    case ActionTypes.STORE_ACCOUNT_INFO:
      return merge(
        state,
        action.payload,
        {
          paymentDetails: { firstName: action.payload.firstName, lastName: action.payload.lastName }
        },
        {
          billingAddress: {
            address: action.payload.address,
            city: action.payload.city,
            state: action.payload.state
          }
        }
      )
    case ActionTypes.STORE_PAYMENT_INFO:
      return {
        ...state,
        currency: action.currency,
        billingAddress: { ...state.billingAddress, ...action.billingAddress },
        paymentDetails: { ...state.paymentDetails, ...action.paymentDetails }
      }
    case ServiceSpecsTypes.FILL_SERVICE_SPECS:
      return {
        ...state,
        balance: action.payload.balance,
        currency: action.payload.currency
      }
    default:
      return state
  }
}
