import React from 'react'
import styled from 'styled-components'
import { FormattedMessage } from 'react-intl'
import { Button, Checkbox, LinearProgress } from '@material-ui/core'
import { cond, equals, always } from 'ramda'

import Formatted from '../../components/Formatted'
import { BasicInput } from '../../components/Inputs'
import injectFormatMessage from '../../hocs/injectFormatMessage'

const Wrapper = styled.div`
  > h1 {
    padding: 30px 0 47px;
    font-family: 'Open Sans';
    font-size: 2.25rem;
    font-weight: 400;
    text-align: center;
    color: #7d7d7d;
  }

  .errorable {
    position: relative;

    .error-block {
      position: absolute;
      top: 59%;
      left: 0;
      font-family: 'Open Sans';
      font-size: 0.875rem;
      color: #ee4236;
    }
  }

  > form {
    max-width: 850px;
    width: 100%;
    margin: 0 auto;
  }

  .form-group {
    flex: 1;
    max-width: 405px;
  }

  .select {
    width: 100%;
    margin: 0 0 44px;

    > label {
      color: #7d7d7d;
    }

    > div::after {
      border-bottom: 2px solid #09d357;
    }
  }

  .progress-container {
    max-width: 320px;
    width: 100%;

    > div {
      flex: 1;
      height: 7px;
      border-radius: 9px;

      > div {
        height: 7px;
        background-color: #09d357;
      }
    }

    > span {
      display: block;
      min-width: 40px;
      margin: 0 0 0 20px;
      font-family: 'Open Sans';
      font-size: 1.1875rem;
      font-weight: 400;
    }
  }

  .custom-basic-input {
    margin-bottom: 32px !important;
  }

  .checkbox-container {
    margin: 20px 0 0;

    label {
      font-size: 0.9375rem;
      color: #43425d;
      cursor: pointer;
      user-select: none;

      .checkbox {
        padding-top: 2px;
        padding-left: 0;
      }

      > span {
        vertical-align: middle;

        &:nth-child(2) {
          display: block;
          font-family: 'Open Sans';
          font-size: 1rem;
          font-weight: 400;
          line-height: 1.5rem;
        }
      }
    }
  }
`

const RegistrationStep2 = ({
  firstName,
  password,
  confirmPassword,
  passwordLevel,
  onChange,
  onSubmit,
  submitted,
  formatMessage
}) => (
  <Wrapper>
    <Formatted.h1 id="wizard.registration.step2.title" />
    <form className="flex justify-between" onSubmit={onSubmit}>
      <div className="form-group">
        <div className="errorable">
          <BasicInput
            className="custom-basic-input"
            fullWidth
            inputProps={{ tabIndex: '5' }}
            error={submitted && !firstName}
            name="firstName"
            onChange={onChange}
            style={{ height: 38 }}
            placeholder={formatMessage({ id: 'global.firstName' })}
          />
          {submitted && !firstName && (
            <div className="error-block">
              <FormattedMessage id="global.requiredField" />
            </div>
          )}
        </div>
        <BasicInput
          className="custom-basic-input"
          fullWidth
          inputProps={{ tabIndex: '6' }}
          name="lastName"
          onChange={onChange}
          style={{ height: 38 }}
          placeholder={formatMessage({ id: 'global.lastName' })}
        />
        <div className="errorable">
          <BasicInput
            className="custom-basic-input"
            fullWidth
            inputProps={{ tabIndex: '7' }}
            error={submitted && !password}
            type="password"
            name="password"
            onChange={onChange}
            style={{ height: 38 }}
            placeholder={formatMessage({ id: 'global.password' })}
          />
          {submitted && !password && (
            <div className="error-block">
              <FormattedMessage id="global.requiredField" />
            </div>
          )}
        </div>
        <Button style={{ color: '#09d357', textTransform: 'uppercase', margin: '-7px 0 0 -5px' }}>
          auto generate password
        </Button>
      </div>
      <div className="form-group">
        <BasicInput
          className="custom-basic-input"
          fullWidth
          inputProps={{ tabIndex: '5' }}
          type="tel"
          name="phoneNumber"
          onChange={onChange}
          style={{ height: 38 }}
          placeholder={formatMessage({ id: 'global.phoneNumber' })}
        />
        <BasicInput
          className="custom-basic-input"
          fullWidth
          inputProps={{ tabIndex: '6' }}
          type="email"
          name="email"
          onChange={onChange}
          style={{ height: 38 }}
          placeholder={formatMessage({ id: 'global.email' })}
        />
        <div className="errorable">
          <BasicInput
            className="custom-basic-input"
            fullWidth
            inputProps={{ tabIndex: '7' }}
            error={submitted && (password !== confirmPassword || !confirmPassword)}
            type="password"
            name="confirmPassword"
            onChange={onChange}
            style={{ height: 38 }}
            placeholder={formatMessage({ id: 'global.confirmPassword' })}
          />
          {submitted && password !== confirmPassword ? (
            <div className="error-block">
              <FormattedMessage id="global.confirmPasswordMismatch" />
            </div>
          ) : submitted && !confirmPassword ? (
            <div className="error-block">
              <FormattedMessage id="global.requiredField" />
            </div>
          ) : null}
        </div>
        <div className="progress-container flex justify-between align-center">
          <LinearProgress value={passwordLevel} variant="determinate" />
          <span>
            {cond([
              [equals(0), always('Bad')],
              [equals(50), always('Good')],
              [equals(100), always('Best')]
            ])(passwordLevel)}
          </span>
        </div>
        <div className="checkbox-container">
          <label className="flex align-start">
            <Checkbox className="checkbox" />
            <FormattedMessage id="wizard.registration.credentialEmail.checkbox" />
          </label>
        </div>
      </div>
    </form>
  </Wrapper>
)

export default injectFormatMessage(RegistrationStep2)
