/** @flow */

import { createStore, applyMiddleware, compose } from 'redux'
import rootReducer from './rootReducer'

type Middleware = (store: Object) => (next: Function) => (action: Function | Object) => any

const configureStore = (preloadedState?: Object, extraMiddlewares?: Array<Middleware> = []) => {
  const composeEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose

  return createStore<Object, Object, ?(Function | Object)>(
    rootReducer,
    preloadedState,
    process.env.NODE_ENV === 'development'
      ? composeEnhancer(applyMiddleware(...extraMiddlewares))
      : applyMiddleware(...extraMiddlewares)
  )
}

export default configureStore
