// @flow

import React from 'react'
import { injectIntl } from 'react-intl'
import type { IntlShape } from 'react-intl'

type Props = {
  intl: IntlShape
}

const withFormatMessage: any = (WrappedComponent: React$ComponentType<*>) => {
  const componentWithFormatMessage = ({ intl, ...props }: Props) => (
    <WrappedComponent formatMessage={intl.formatMessage} {...props} />
  )

  componentWithFormatMessage.displayName = `withFormatMessage(${WrappedComponent.displayName ||
    'Component'})`

  return injectIntl(componentWithFormatMessage)
}

export default withFormatMessage
