/** @flow */

import Cookies from 'universal-cookie'
import { has } from 'ramda'
import { DEFAULT_LOCALE, LOCALES } from '../../i18n'
import * as ActionTypes from './action'

const initialState = {
  locale: new Cookies().get('locale') || DEFAULT_LOCALE
}

export default function i18n(state: Object = initialState, action: Object) {
  switch (action.type) {
    case ActionTypes.LOCALE_CHANGE:
      return {
        ...state,
        locale: has(action.locale)(LOCALES) ? action.locale : DEFAULT_LOCALE
      }
    default:
      return state
  }
}
