/** @flow */

const NAME = '[ACCOUNT]'

/**
 * Stores account information
 */

export type AccountInfo = {
  firstName?: string,
  lastName?: string,
  email?: string,
  adminPassword?: string,
  confirmPassword?: string,
  password?: string,
  contactNumber?: string,
  companyName: string,
  accountDomain?: string,
  address?: string,
  zip?: string,
  city?: string,
  state?: string | number,
  timeZone?: string | number,
  language?: string | number
}

export const STORE_ACCOUNT_INFO = `${NAME} STORE_ACCOUNT_INFO`
export function storeAccountInfo(info: AccountInfo) {
  return {
    type: STORE_ACCOUNT_INFO,
    payload: info
  }
}

/**
 * Stores payment information for a current account
 */

export type BillingAddress = {
  country: string,
  fullName: string,
  address: string,
  city: string,
  state: string
}
export type PaymentDetails = {
  firstName: string,
  lastName: string,
  cardNumber: string,
  exp: string,
  cvv: string
}
export type PaymentInfo = {
  currency: string | number,
  billingAddress: BillingAddress,
  paymentDetails: PaymentDetails
}

export const STORE_PAYMENT_INFO = `${NAME} STORE_PAYMENT_INFO`
export function storePaymentInfo({ currency, billingAddress, paymentDetails }: PaymentInfo) {
  return {
    type: STORE_PAYMENT_INFO,
    currency,
    billingAddress,
    paymentDetails
  }
}
