import React from 'react'
import styled from 'styled-components'
import { CircularProgress } from '@material-ui/core'

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10000;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  animation: 0.3s forwards 0s fadeIn;

  @keyframes fadeIn {
    from {
      opacity: 0;
      pointer-events: none;
    }
    to {
      opacity: 1;
      pointer-events: initial;
    }
  }

  .spinner {
    color: #09d357;
    z-index: 100001;
  }
`

const Loader = ({ visible }) =>
  visible && (
    <Wrapper>
      <CircularProgress className="spinner" size={100} thickness={5} />
    </Wrapper>
  )

export default Loader
