/** @flow */

import { addLocaleData } from 'react-intl'
//$FlowFixMe
import en from 'react-intl/locale-data/en'
import enMessages from './en.json'

type Messages = { [messageId: string]: string }
type FormatTranslationMessages = (locale: string, messsages: Object) => Messages
type FormattedMessages = {
  [locale: string]: Messages
}

export const LOCALES: Locales = { 'en-US': 1 }
export const DEFAULT_MESSAGES: Messages = enMessages
export const DEFAULT_LOCALE: string = 'en-US'
export const CURRENCIES: CurrencyEnum = { usd: 1, eur: 2 }

export const formatTranslationMessages: FormatTranslationMessages = (locale, messages) =>
  Object.keys(messages).reduce(
    (formattedMessages, key) => ({
      ...formattedMessages,
      [key]: !messages[key] ? DEFAULT_MESSAGES[key] : messages[key]
    }),
    {}
  )

export const formattedMessages: FormattedMessages = {
  'en-US': formatTranslationMessages('en-US', enMessages)
}

export default () => addLocaleData(en)
