/** @flow */

import React from 'react'
import { connect } from 'react-redux'
import { IntlProvider } from 'react-intl'
import { formattedMessages } from './i18n'
import Router from './pages'

type Props = {
  locale: string
}

const App = ({ locale }: Props) => (
  <IntlProvider
    key={locale}
    locale={locale}
    messages={formattedMessages[locale]}
    defaultLocale="en"
  >
    <>
      <Router />
    </>
  </IntlProvider>
)

type MapStateToProps = (state: Object) => Props

const mstp: MapStateToProps = ({ i18n }) => ({ locale: i18n.locale })

export default connect(mstp)(App)
