/* eslint-disable react/display-name */

import * as React from 'react'
import { FormattedMessage } from 'react-intl'

const withFormat = tagName => ({ id, values, ...rest }) =>
  React.createElement(tagName, { ...rest }, React.createElement(FormattedMessage, { id, values }))

class Formatted {
  static h1 = withFormat('h1')
  static h2 = withFormat('h2')
  static h3 = withFormat('h3')
  static h4 = withFormat('h4')
  static p = withFormat('p')
  static label = withFormat('label')
  static small = withFormat('small')
  static strong = withFormat('strong')
  static b = withFormat('b')
  static em = withFormat('em')
  static i = withFormat('i')
}

export default Formatted
