import React from 'react'
import styled from 'styled-components'
import { cond, equals, always, T } from 'ramda'
import { withRouter, Switch, Redirect, Route } from 'react-router-dom'

import ROUTES from '../routes'
import Navbar from './Navbar'
import Users from './users'
import CallFlow from './call-flow'
import SystemService from './system-service'

const Wrapper = styled.div`
  min-height: 100vh;

  > section {
    flex: 1;
    max-width: 1374px;
    width: 100%;
    padding: 65px 0 0 71px;

    > h1 {
      margin: 0 0 
      font-family: 'Open Sans';
      font-size: 2rem;
      font-weight: 400;
      text-transform: uppercase;
      color: #000;
    }
  }

  @media (max-width: 1300px) {
    > section {
      padding: 65px 0 0 30px;
    }
  }
`

const Dashboard = ({ location }) => {
  const getHeader = cond([
    [equals(ROUTES.users), always('users')],
    [equals(ROUTES.groups), always('groups')],
    [equals(ROUTES.callFlow), always('Main number Call-flow')],
    [equals(ROUTES.systemService), always('manage system service ')],
    [T, always('dashboard')]
  ])

  return (
    <Wrapper className="flex">
      <Navbar />
      <section>
        <h1>{getHeader(location ? location.pathname || '' : '')}</h1>
        <Switch>
          <Redirect exact from={ROUTES.dashboard} to={ROUTES.users} />
          <Route path={ROUTES.users} component={Users} />
          <Route path={ROUTES.callFlow} component={CallFlow} />
          <Route path={ROUTES.systemService} component={SystemService} />
        </Switch>
      </section>
    </Wrapper>
  )
}

export default withRouter(Dashboard)
