/** @flow */

import * as ActionTypes from './action'

const initialState = {
  step: 0
}

export default function wizard(state: Object = initialState, action: Object) {
  switch (action.type) {
    case ActionTypes.STEP_CHANGE:
      return { ...state, step: action.step }
    case ActionTypes.RESET_WIZARD:
      return { ...state, step: 0 }
    default:
      return state
  }
}
