import React from 'react'
import styled from 'styled-components'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter, Link } from 'react-router-dom'
import { cond, equals } from 'ramda'
import { FormattedMessage } from 'react-intl'
import { Fab, withStyles } from '@material-ui/core'
import classNames from 'classnames'
import SwipeableViews from 'react-swipeable-views'

import { addUser } from '../../store/users/action'
import { changeStep } from '../../store/wizard/action'
import { storeAccountInfo, storePaymentInfo } from '../../store/account/action'
import { fillServiceSpecs } from '../../store/serice-specs/action'
import ROUTES from '../../pages/routes'
import Logo from '../../components/Logo'
import Loader from '../../components/Loader'
import Box from './Box'
import Steppers from './Steppers'
import Registration from './Registration'
import RegistrationStep2 from './RegistrationStep2'
import Specification from './Specification'
import Payment from './Payment'

const Wrapper = styled.div`
  min-height: 100vh;
  background: #f2f2f2;

  > .container {
    max-width: 1690px;
    width: 100%;

    > header {
      padding: 25px 0 0;

      > a svg {
        max-width: 91px;
        max-height: 68px;
        margin: 0 18px;
      }

      > span {
        font-family: 'Open Sans';
        font-size: 1.3rem;
        color: #a8a8a8;
      }
    }

    > .box {
      margin: 30px auto 0;
    }
  }

  .btns-container {
    margin: 16px 0 0;
  }
`

const styles = {
  fab: {
    width: 162,
    height: 58,
    fontSize: '18px',
    fontFamily: 'Source Sans Pro',
    textTransform: 'inherit',
    borderRadius: '29px'
  },
  next: {
    background: '#09d357',
    color: '#fff',
    '&:hover': {
      background: '#09d357',
      color: '#fff'
    }
  },
  back: {
    background: '#aaa',
    color: '#fff',
    '&:hover': {
      background: '#aaa',
      color: '#fff'
    }
  }
}

class Wizard extends React.PureComponent {
  state = {
    registrationStep2Submitted: false,
    firstName: '',
    lastName: '',
    password: '',
    accountDomain: '',
    zipCode: '',
    confirmPassword: '',
    phoneNumber: '',
    email: '',
    companyName: '',
    address: '',
    city: '',
    timeZone: '',
    language: '',
    country: '',
    numOfUsers: 1,
    twoWayTrunks: 10,
    inboundTrunks: 50,
    outboundTrunks: 20,
    currency: '',
    balance: 100,
    callRecordStorage: 1,
    hasRegistrationError: false,
    billingAddress: {},
    paymentDetails: {},
    passwordLevel: 0,
    showLoader: false
  }

  componentWillUnmount() {
    if (this.timer) clearTimeout(this.timer)
  }

  handleInputChange = event => {
    const { name, value } = event.target
    switch (name) {
      case 'password': {
        this.setState({
          [name]: value,
          passwordLevel: value.length < 4 ? 0 : value.length >= 4 && value.length < 8 ? 50 : 100
        })
        break
      }
      case 'numOfUsers': {
        this.setState({ [name]: value, specificationError: !value || value <= 0 })
        break
      }
      default:
        this.setState({ [name]: value })
    }
  }

  handleSliderChange = name => (_, value) => this.setState({ [name]: value })

  handleBillingChange = event => {
    const { name, value } = event.target
    const { billingAddress } = this.state
    this.setState({ billingAddress: { ...billingAddress, [name]: value }, [name]: value })
  }

  handlePaymentChange = event => {
    const { name, value } = event.target
    const { paymentDetails } = this.state
    this.setState({ paymentDetails: { ...paymentDetails, [name]: value }, [name]: value })
  }

  handleRegistrationSubmit = () => {
    if (!this.state.companyName) {
      this.setState({ hasRegistrationError: true })
    } else {
      this.setState({ hasRegistrationError: false })
      this.props.storeAccountInfo({
        companyName: this.state.companyName,
        accountDomain: this.state.accountDomain,
        address: this.state.address,
        zipCode: this.state.zipCode,
        city: this.state.city,
        timeZone: this.state.timeZone,
        language: this.state.language,
        country: this.state.country
      })
      this.props.changeStep(1)
    }
  }

  handleRegistrationStep2Submit = () => {
    const { firstName, password, confirmPassword } = this.state
    if (!firstName || password !== confirmPassword || !confirmPassword) {
      this.setState({ registrationStep2Submitted: true })
    } else {
      this.setState({ registrationStep2Submitted: false }, () => {
        this.props.storeAccountInfo({
          firstName: this.state.firstName,
          lastName: this.state.lastName,
          password: this.state.password,
          confirmPassword: this.state.confirmPassword,
          phoneNumber: this.state.phoneNumber,
          email: this.state.email
        })
        this.props.addUser({ firstName: this.state.firstName, lastName: this.state.lastName })
        this.props.changeStep(2)
      })
    }
  }

  handleSpecificationSubmit = () => {
    const { numOfUsers } = this.state
    if (!numOfUsers || numOfUsers <= 0) {
      this.setState({ specificationError: true, specificationSubmitted: true })
    } else {
      this.setState({ specificationError: false, specificationSubmitted: false }, () => {
        this.props.fillServiceSpecs({
          numberOfUsers: this.state.numOfUsers,
          twoWayTrunks: this.state.twoWayTrunks,
          inboundTrunks: this.state.inboundTrunks,
          outboundTrunks: this.state.outboundTrunks,
          currency: this.state.currency,
          balance: this.state.balance,
          callRecordStorage: this.state.callRecordStorage
        })
        this.props.changeStep(3)
      })
    }
  }

  handlePaymentSubmit = () => {
    this.props.storePaymentInfo({
      currency: this.state.currency,
      billingAddress: this.state.billingAddress,
      paymentDetails: this.state.paymentDetails
    })
    this.finishWizard()
  }

  finishWizard = () => {
    this.setState({ showLoader: true }, () => {
      this.timer = setTimeout(
        () => this.setState({ showLoader: false }, () => this.props.history.push(ROUTES.dashboard)),
        3000
      )
    })
  }

  handleMoveNext = () =>
    cond([
      [equals(0), this.handleRegistrationSubmit],
      [equals(1), this.handleRegistrationStep2Submit],
      [equals(2), this.handleSpecificationSubmit],
      [equals(3), this.handlePaymentSubmit]
    ])(this.props.step)

  handleMovePrev = () => {
    const { step } = this.props
    this.props.changeStep(step ? step - 1 : step)
  }

  render() {
    const { classes, step } = this.props
    const { showLoader } = this.state

    return (
      <Wrapper>
        <Loader visible={showLoader} />
        <div className="container">
          <header className="flex justify-between align-end">
            <Link to={ROUTES.landing}>
              <Logo color="#09d357" />
            </Link>
            <span className="fake-anchor">Help</span>
          </header>
          <Box className="box">
            <Steppers />
            <SwipeableViews disabled index={step}>
              <Registration
                timeZone={this.state.timeZone || ''}
                language={this.state.language || ''}
                country={this.state.country || ''}
                hasError={this.state.hasRegistrationError || false}
                onChange={this.handleInputChange}
                onSubmit={this.handleRegistrationSubmit}
              />
              <RegistrationStep2
                submitted={this.state.registrationStep2Submitted || false}
                firstName={this.state.firstName || ''}
                password={this.state.password || ''}
                passwordLevel={this.state.passwordLevel || 0}
                confirmPassword={this.state.confirmPassword || ''}
                onChange={this.handleInputChange}
                onSubmit={this.handleRegistrationStep2Submit}
              />
              <Specification
                balance={this.state.balance || '0'}
                callRecordStorage={this.state.callRecordStorage || '0'}
                numOfUsers={this.state.numOfUsers || '0'}
                twoWayTrunks={this.state.twoWayTrunks || 0}
                inboundTrunks={this.state.inboundTrunks || 0}
                outboundTrunks={this.state.outboundTrunks || 0}
                currency={this.state.currency || ''}
                specificationError={this.state.specificationError}
                submitted={this.state.specificationSubmitted || false}
                onChange={this.handleInputChange}
                onSliderChange={this.handleSliderChange}
              />
              <Payment
                numOfUsers={this.state.numOfUsers}
                firstName={this.state.firstName || ''}
                lastName={this.state.lastName || ''}
                currency={this.state.currency}
                address={this.state.address || ''}
                city={this.state.city || ''}
                state={this.state.state || ''}
                country={this.state.country || ''}
                onChange={this.handleInputChange}
                onBillingChange={this.handleBillingChange}
                onPaymentChange={this.handlePaymentChange}
              />
            </SwipeableViews>
            <div className="btns-container flex justify-between">
              <Fab
                variant="extended"
                aria-label="Back"
                className={classNames(classes.fab, classes.back)}
                style={{ visibility: step === 0 ? 'hidden' : 'visible' }}
                onClick={this.handleMovePrev}
              >
                <FormattedMessage id="global.back" />
              </Fab>
              <Fab
                variant="extended"
                aria-label="Next"
                className={classNames(classes.fab, classes.next)}
                onClick={this.handleMoveNext}
              >
                <FormattedMessage id="global.next" />
              </Fab>
            </div>
          </Box>
        </div>
      </Wrapper>
    )
  }
}

const enhance = compose(
  withStyles(styles),
  withRouter,
  connect(
    ({ wizard }) => ({ step: wizard.step }),
    { changeStep, addUser, storeAccountInfo, fillServiceSpecs, storePaymentInfo }
  )
)

export default enhance(Wizard)
