import React from 'react'
import styled from 'styled-components'
import { connect } from 'react-redux'
import { Fab, withStyles } from '@material-ui/core'
import AddIcon from '@material-ui/icons/Add'

import { toggleUserModal } from '../../../store/users/action'
import Header from './Header'
import TableContainer from './TableContainer'
import UserModal from './UserModal'

const Wrapper = styled.section`
  font-size: 1rem;

  .users-header {
    margin: 62px 0 0;
  }

  .table-container {
    margin: 38px 0 0;
  }

  .add-button svg {
    transition: color 0.3s;
  }
  .add-button:hover svg {
    color: #000;
  }

  @media (max-width: 1300px) {
    font-size: 0.8rem;
  }
`

const styles = {
  fab: {
    position: 'fixed',
    zIndex: '12',
    right: '5vw',
    bottom: '8vh',
    background: '#09d357',
    '&:hover': { background: '#fff' }
  },
  addIcon: {
    color: '#fff'
  }
}

class Users extends React.Component {
  state = {
    searchKey: ''
  }

  handleFeatureClick = (user, featureId) => () => {
    if ([2, 4, 8].includes(featureId))
      this.props.toggleUserModal({ id: user.id, force: true, featureId, view: 'featureToggle' })
  }

  handleAddNewUser = () => this.props.toggleUserModal({ force: true, view: 'add' })

  render() {
    const { isModalOpen, classes } = this.props

    return (
      <Wrapper>
        {isModalOpen && <UserModal />}
        <Header
          className="users-header"
          onAddNewUser={() => this.props.toggleUserModal({ force: true, view: 'add' })}
        />
        <TableContainer
          className="table-container"
          onAddNewUser={() => this.props.toggleUserModal({ force: true, view: 'add' })}
          onFeaturesClick={id => this.props.toggleUserModal({ force: true, id, view: 'features' })}
          onFeatureClick={this.handleFeatureClick}
        />
        <Fab
          aria-label="Add"
          className={`${classes.fab} add-button`}
          onClick={this.handleAddNewUser}
        >
          <AddIcon className={classes.addIcon} />
        </Fab>
      </Wrapper>
    )
  }
}

export default connect(
  ({ users }) => ({ isModalOpen: users.isModalOpen }),
  { toggleUserModal }
)(withStyles(styles)(Users))
