/** @flow */

import React from 'react'
import { Input, withStyles } from '@material-ui/core'
import classNames from 'classnames'

const styles = {
  input: {
    margin: '0 0 44px',
    fontFamily: 'Open Sans',
    fontSize: '0.9375rem',
    lineHeight: '25px',
    '&::after': {
      borderBottom: '2px solid #09d357'
    }
  }
}

type Props = {
  classes: Object,
  className?: string
}

const BasicInput = ({ classes, className = '', ...rest }: Props) => (
  <Input {...rest} className={classNames(classes.input, className)} />
)

export default withStyles(styles)(BasicInput)
