import React from 'react'
import styled from 'styled-components'
import { FormattedMessage } from 'react-intl'
import { FormControl, InputLabel, Select, MenuItem } from '@material-ui/core'

import timeZones from '../../constants'
import Formatted from '../../components/Formatted'
import { BasicInput } from '../../components/Inputs'
import injectFormatMessage from '../../hocs/injectFormatMessage'

const Wrapper = styled.div`
  > h1 {
    padding: 30px 0 47px;
    font-family: 'Open Sans';
    font-size: 2.25rem;
    font-weight: 400;
    text-align: center;
    color: #7d7d7d;
  }

  .company-name-input {
    position: relative;

    .error-block {
      position: absolute;
      top: 50%;
      left: 0;
      font-family: 'Open Sans';
      font-size: 0.875rem;
      color: #ee4236;
    }
  }

  > form {
    max-width: 850px;
    width: 100%;
    margin: 0 auto;
  }

  .form-group {
    flex: 1;
    max-width: 405px;
  }

  .select {
    width: 100%;
    margin: 0 0 44px;

    > label {
      color: #7d7d7d;
    }

    > div::after {
      border-bottom: 2px solid #09d357;
    }
  }
`

const Registration = ({
  timeZone,
  language,
  country,
  hasError,
  onSubmit,
  onChange,
  formatMessage
}) => (
  <Wrapper>
    <Formatted.h1 id="wizard.registration.title" />
    <form className="flex justify-between" onSubmit={onSubmit}>
      <div className="form-group">
        <div className="company-name-input">
          <BasicInput
            fullWidth
            inputProps={{ tabIndex: '1' }}
            error={hasError}
            name="companyName"
            onChange={onChange}
            placeholder={formatMessage({ id: 'wizard.registration.companyName.placeholder' })}
          />
          {hasError && (
            <div className="error-block">
              <FormattedMessage id="global.requiredField" />
            </div>
          )}
        </div>
        <BasicInput
          fullWidth
          inputProps={{ tabIndex: '2' }}
          name="address"
          onChange={onChange}
          placeholder={formatMessage({ id: 'global.address' })}
        />
        <BasicInput
          fullWidth
          inputProps={{ tabIndex: '3' }}
          name="city"
          onChange={onChange}
          placeholder={formatMessage({ id: 'global.city' })}
        />
        <FormControl classes={{ root: 'select' }}>
          <InputLabel htmlFor="timeZone">
            <FormattedMessage id="global.timeZone" />
          </InputLabel>
          <Select
            style={{ marginBottom: '0' }}
            value={timeZone}
            onChange={onChange}
            inputProps={{
              name: 'timeZone',
              id: 'timeZone',
              tabIndex: '4'
            }}
          >
            <MenuItem value={''} />
            {timeZones.map((zone, index) => (
              <MenuItem key={zone} value={index}>
                {zone}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </div>
      <div className="form-group">
        <BasicInput
          fullWidth
          inputProps={{ tabIndex: '1' }}
          name="accountDomain"
          onChange={onChange}
          placeholder={formatMessage({ id: 'wizard.registration.accountDomain.placeholder' })}
        />
        <BasicInput
          fullWidth
          inputProps={{ tabIndex: '2' }}
          name="zipCode"
          onChange={onChange}
          placeholder={formatMessage({ id: 'global.zipCode' })}
          style={{ marginBottom: '34px' }}
        />
        <FormControl classes={{ root: 'select' }}>
          <InputLabel htmlFor="country">Country</InputLabel>
          <Select
            value={country}
            onChange={onChange}
            inputProps={{
              name: 'country',
              id: 'country',
              tabIndex: '3'
            }}
          >
            <MenuItem value={''} />
            <MenuItem value={1}>United States</MenuItem>
            <MenuItem value={2}>Thailand</MenuItem>
            <MenuItem value={3}>Vietnam</MenuItem>
            <MenuItem value={4}>Israel</MenuItem>
          </Select>
        </FormControl>
        <FormControl classes={{ root: 'select' }}>
          <InputLabel htmlFor="language">
            <FormattedMessage id="global.language" />
          </InputLabel>
          <Select
            style={{ marginBottom: '0' }}
            value={language}
            onChange={onChange}
            inputProps={{
              name: 'language',
              id: 'language',
              tabIndex: '4'
            }}
          >
            <MenuItem value={''} />
            <MenuItem value={1}>English</MenuItem>
            <MenuItem value={2}>Thai</MenuItem>
            <MenuItem value={3}>Vietnamese</MenuItem>
            <MenuItem value={4}>Hebrew</MenuItem>
          </Select>
        </FormControl>
      </div>
    </form>
  </Wrapper>
)

export default injectFormatMessage(Registration)
