/** @flow */

import * as ActionTypes from './action'

const initialState = {
  numberOfUsers: 0,
  twoWayTrunks: 0,
  inboundTrunks: 0,
  outboundTrunks: 0,
  permitPerMinuteCalls: true,
  callRecordingStorage: 0
}

export default function serviceSpecs(state: Object = initialState, action: Object) {
  switch (action.type) {
    case ActionTypes.FILL_SERVICE_SPECS:
      return { ...state, ...action.payload }
    default:
      return state
  }
}
