/** @flow */

const NAME = '[USERS]'

/**
 * Adds user using Dialog
 */

export type AddUserParams = {
  firstName: string,
  lastName: string,
  email: string,
  password: string,
  confirmPassword: string,
  features: Array<*>,
  alternateEmail: string,
  devices: Array<{ extensionNumber: string, directPhoneNumber: string }>,
  userLevel: number,
  locale: number,
  timeZone: string,
  ringingTime: number
}

export const ADD_USER = `${NAME} ADD_USER`
export function addUser(user: AddUserParams) {
  return {
    type: ADD_USER,
    payload: user
  }
}

export const TOGGLE_FEATURE = `${NAME} TOGGLE_FEATURE`
export function toggleFeature({ checked, userId, featureId }) {
  return {
    type: TOGGLE_FEATURE,
    checked,
    userId,
    featureId
  }
}

export type ToggleUserModal = {
  force?: boolean,
  id: string | number,
  view: string | number,
  featureId: ?(string | number)
}

export const TOGGLE_USER_MODAL = `${NAME} TOGGLE_USER_MODAL`
export function toggleUserModal({ force, id, view, featureId }: ToggleUserModal) {
  return {
    type: TOGGLE_USER_MODAL,
    force,
    id,
    view,
    featureId
  }
}
