/* eslint-disable react/no-unescaped-entities */
import React from 'react'
import styled from 'styled-components'
import {
  Switch,
  withStyles,
  Input,
  Checkbox,
  FormControl,
  InputLabel,
  Select,
  MenuItem
} from '@material-ui/core'
import { connect } from 'react-redux'
import { compose } from 'redux'

const Wrapper = styled.div`
  .aligner {
    max-width: 650px;
    margin: 0 5% 0 20%;
  }

  h3 {
    margin: 0 0 12px;
    color: #707070;
    font-family: 'Open Sans';
    font-size: 1.375rem;
    font-weight: 700;
  }

  .basic-input {
    font-family: 'Open Sans';
    font-size: 1.25rem;
    font-weight: 400;

    &::after {
      border-bottom: 2px solid #09d357;
    }
  }

  .checkbox-container > label > span:nth-child(2) {
    padding-top: 2px;
    color: #777676;
    font-family: 'Open Sans';
    font-size: 0.85rem;
    font-weight: 400;
    cursor: pointer !important;
  }

  .checkbox {
    padding-top: 0;
    padding-left: 0;
  }

  .already-checkbox > span {
    padding: 0 10px 0 0;
  }

  .input-container {
    margin-bottom: 10px;
  }

  .select {
    margin-top: -23px;
    width: 229px;

    > label {
      color: #7d7d7d;
    }

    > div::after {
      border-bottom: 2px solid #09d357;
    }
  }
`

const styles = theme => ({
  iOSSwitchBase: {
    '&$iOSChecked': {
      color: theme.palette.common.white,
      '& + $iOSBar': {
        backgroundColor: '#52d869'
      }
    },
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
      easing: theme.transitions.easing.sharp
    })
  },
  iOSChecked: {
    transform: 'translateX(15px)',
    '& + $iOSBar': {
      opacity: 1,
      border: 'none'
    }
  },
  iOSBar: {
    borderRadius: 13,
    width: 42,
    height: 26,
    marginTop: -13,
    marginLeft: -21,
    border: 'solid 1px',
    borderColor: theme.palette.grey[400],
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border'])
  },
  iOSIcon: {
    width: 24,
    height: 24
  },
  iOSIconChecked: {
    boxShadow: theme.shadows[1]
  }
})

const VoiceMailFeature = ({ classes, onToggle, features }) => {
  const [unavilableMessage, setUnavilableMessage] = React.useState('')

  return (
    <Wrapper>
      <div className="aligner">
        <h3>
          Voice Mail
          <Switch
            disableRipple
            classes={{
              switchBase: classes.iOSSwitchBase,
              bar: classes.iOSBar,
              icon: classes.iOSIcon,
              iconChecked: classes.iOSIconChecked,
              checked: classes.iOSChecked
            }}
            checked={features[4] || false}
            onChange={onToggle}
          />
        </h3>
        <div className="input-container checkbox-container flex">
          <Input
            style={{ maxWidth: '312px', margin: '0 0 35px' }}
            className="basic-input"
            type="tel"
            placeholder="PIN Number"
          />
          <label
            className="flex align-start already-checkbox"
            style={{ marginTop: '10px', marginLeft: '30px' }}
          >
            <Checkbox className="checkbox" />
            <span>Already Setup</span>
          </label>
        </div>
        <div className="input-container checkbox-container flex align-center">
          <label className="flex align-start">
            <Checkbox className="checkbox" checked={true} />
            <FormControl classes={{ root: 'select' }}>
              <InputLabel htmlFor="unavilable-message">Unavailable Message</InputLabel>
              <Select
                value={unavilableMessage}
                onChange={e => setUnavilableMessage(e.target.value)}
                inputProps={{
                  name: 'unavilableMessage',
                  id: 'unavilable-message'
                }}
              >
                <MenuItem value={''} />
                <MenuItem value={1}>Unavilable 1</MenuItem>
                <MenuItem value={2}>Unavilable 2</MenuItem>
                <MenuItem value={3}>Unavilable 3</MenuItem>
              </Select>
            </FormControl>
          </label>
        </div>
        <div className="input-container checkbox-container">
          <label className="flex align-start">
            <Checkbox className="checkbox" />
            <span>Temporary Message</span>
          </label>
        </div>
        <div className="input-container checkbox-container">
          <label className="flex align-start">
            <Checkbox className="checkbox" />
            <span>Voicemail to e-mail</span>
          </label>
        </div>
        <div className="input-container checkbox-container">
          <label className="flex align-start">
            <Checkbox className="checkbox" />
            <span>Delete messages from database after sending the notification</span>
          </label>
        </div>
      </div>
    </Wrapper>
  )
}

const mstp = ({ users }) => {
  const { target } = users

  if (!target || !target.features) return { features: {} }

  const { id } = target

  return {
    features: (users.users.find(user => user.id === id) || {}).features || {}
  }
}

const enhance = compose(
  withStyles(styles),
  connect(mstp)
)

export default enhance(VoiceMailFeature)
