import React from 'react'
import styled from 'styled-components'
import {
  Switch,
  FormControl,
  Fab,
  InputLabel,
  Select,
  MenuItem,
  withStyles
} from '@material-ui/core'
import { connect } from 'react-redux'
import { compose } from 'redux'

const Wrapper = styled.div`
  max-width: 500px;
  margin: 0 0 0 180px;

  > h3 {
    margin: 0 0 20px;
    color: #707070;
    font-family: 'Open Sans';
    font-size: 1.375rem;
    font-weight: 700;
  }

  .select {
    width: 270px;

    > label {
      color: #7d7d7d;
    }

    > div::after {
      border-bottom: 2px solid #09d357;
    }
  }

  .input-container {
    min-height: 100px;
  }
`

const styles = theme => ({
  fab: {
    width: 98,
    height: 33,
    fontSize: '18px',
    fontFamily: 'Source Sans Pro',
    textTransform: 'inherit',
    borderRadius: '29px',
    background: '#09d357',
    color: '#fff',
    '&:hover': {
      background: '#09d357',
      color: '#fff'
    }
  },
  iOSSwitchBase: {
    '&$iOSChecked': {
      color: theme.palette.common.white,
      '& + $iOSBar': {
        backgroundColor: '#52d869'
      }
    },
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
      easing: theme.transitions.easing.sharp
    })
  },
  iOSChecked: {
    transform: 'translateX(15px)',
    '& + $iOSBar': {
      opacity: 1,
      border: 'none'
    }
  },
  iOSBar: {
    borderRadius: 13,
    width: 42,
    height: 26,
    marginTop: -13,
    marginLeft: -21,
    border: 'solid 1px',
    borderColor: theme.palette.grey[400],
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border'])
  },
  iOSIcon: {
    width: 24,
    height: 24
  },
  iOSIconChecked: {
    boxShadow: theme.shadows[1]
  }
})

const MusicFeature = ({ features, onToggle, classes }) => {
  const [music, setMusic] = React.useState('')

  return (
    <Wrapper>
      <h3>
        Music on Hold
        <Switch
          disableRipple
          classes={{
            switchBase: classes.iOSSwitchBase,
            bar: classes.iOSBar,
            icon: classes.iOSIcon,
            iconChecked: classes.iOSIconChecked,
            checked: classes.iOSChecked
          }}
          checked={features[8] || false}
          onChange={onToggle}
        />
      </h3>
      <div className="input-container flex align-center">
        <FormControl classes={{ root: 'select' }}>
          <InputLabel htmlFor="music">Select music</InputLabel>
          <Select
            value={music}
            onChange={e => setMusic(e.target.value)}
            inputProps={{
              name: 'music',
              id: 'music'
            }}
          >
            <MenuItem value={''}>Silence</MenuItem>
            <MenuItem value={1}>Lolipop</MenuItem>
            <MenuItem value={2}>Directory</MenuItem>
            <MenuItem value={3}>Working hours</MenuItem>
            <MenuItem value={4}>Office close</MenuItem>
            <MenuItem value={5}>Hold Music mp3</MenuItem>
            <MenuItem value={6}>HoldMusic.WAV</MenuItem>
          </Select>
        </FormControl>
        <Fab variant="extended" className={classes.fab} style={{ margin: '0px 40px -13px' }}>
          Upload
        </Fab>
      </div>
    </Wrapper>
  )
}

const mstp = ({ users }) => {
  const { target } = users

  if (!target || !target.features) return { features: {} }

  const { id } = target

  return {
    features: (users.users.find(user => user.id === id) || {}).features || {}
  }
}

const enhance = compose(
  withStyles(styles),
  connect(mstp)
)

export default enhance(MusicFeature)
